.ql-viewer {
  font-size: 0.9em;
  box-sizing: border-box;
  line-height: 1.42;
  height: auto;
  outline: none;
  /* overflow-y: auto; */
  /* padding: 0px 0px; */
  tab-size: 4;
  -moz-tab-size: 4;
  text-align: left;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.ql-viewer blockquote {
  font-weight: 30;
  font-size: 1rem;
  max-width: 600px;
  line-height: 1.4;
  position: relative;
  margin: 0;
  padding: 2rem;
}

.ql-viewer blockquote::before,
.ql-viewer blockquote::after {
  position: absolute;
  color: #754ffe;
  font-size: 5rem;
  width: 4rem;
  height: 4rem;
}

.ql-viewer blockquote::before {
  content: "\201C";
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  left: 0rem;
  top: -3rem;
}

.ql-viewer blockquote::after {
  content: "\201D";
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  right: 0rem;
  bottom: -1rem;
}

.ql-viewer > * {
  cursor: text;
}
.ql-viewer h1 {
  font-size: 1.8em;
  font-weight: 400;
  margin: 0.67em 0;
}
.ql-viewer h2 {
  font-size: 1.4em;
  font-weight: 400;
  margin: 0.2em 0 0.05em;
}
.ql-viewer p {
  font-size: 1.1em;
}
.ql-viewer ol,
.ql-viewer ul {
  font-size: 1.1em;
}
.ql-viewer pre,
.ql-viewer blockquote,
.ql-viewer h3,
.ql-viewer h4,
.ql-viewer h5,
.ql-viewer h6 {
  margin: 0;
  padding: 0;
  counter-reset: list-1 list-2 list-3 list-4 list-5 list-6 list-7 list-8 list-9;
}
.ql-viewer ol,
.ql-viewer ul {
  padding-left: 1.5em;
}
.ql-viewer ol > li,
.ql-viewer ul > li {
  list-style-type: none;
}
.ql-viewer ul > li::before {
  content: "\2022";
}
.ql-viewer ul[data-checked="true"],
.ql-viewer ul[data-checked="false"] {
  pointer-events: none;
}
.ql-viewer ul[data-checked="true"] > li *,
.ql-viewer ul[data-checked="false"] > li * {
  pointer-events: all;
}
.ql-viewer p {
  text-align: justify;
}
/* .ql-viewer p strong {
    font-size: 1.3em;
  } */
.ql-viewer img {
  margin-left: auto;
  margin-right: auto;
}
.ql-viewer ul[data-checked="true"] > li::before,
.ql-viewer ul[data-checked="false"] > li::before {
  color: #777;
  cursor: pointer;
  pointer-events: all;
}
.ql-viewer ul[data-checked="true"] > li::before {
  content: "\2611";
}
.ql-viewer ul[data-checked="false"] > li::before {
  content: "\2610";
}
.ql-viewer li::before {
  display: inline-block;
  white-space: nowrap;
  width: 1.2em;
}
.ql-viewer li:not(.ql-direction-rtl)::before {
  margin-left: -1.5em;
  margin-right: 0.3em;
  text-align: right;
}
.ql-viewer li.ql-direction-rtl::before {
  margin-left: 0.3em;
  margin-right: -1.5em;
}
.ql-viewer ol li:not(.ql-direction-rtl),
.ql-viewer ul li:not(.ql-direction-rtl) {
  padding-left: 1.5em;
}
.ql-viewer ol li.ql-direction-rtl,
.ql-viewer ul li.ql-direction-rtl {
  padding-right: 1.5em;
}
.ql-viewer a {
  color: rgb(79 70 229);
}
.ql-viewer ol li {
  counter-reset: list-1 list-2 list-3 list-4 list-5 list-6 list-7 list-8 list-9;
  counter-increment: list-0;
}
.ql-viewer ol li:before {
  content: counter(list-0, decimal) ". ";
}
.ql-viewer ol li.ql-indent-1 {
